rente en el proceso de la digestión. Otra característica importante es que, a diferencia de los monogástricos, los terneros nace con el rumen poco desarrollado y solo hasta la segunda o tercera semana de edad inician el proceso rumia, convirtiéndose en rumiantes.")]),_c('h3',{staticClass:"mt-5 pt-5"},[_vm._v("Sistema digestivo de los rumiantes")]),_c('p',[_vm._v("Los animales clasificados como rumiantes son los bovinos, ovinos y caprinos, los cuales funcionan de la siguiente manera: mastican la comida aun cuando no ingieren alimentos. Esta acción de masticación se llama rumia, proceso que realizan para destruir las partículas de la fibra, produciendo así la saliva, que se encarga de neutralizar los ácidos producidos por la fermentación.")]),_c('p',[_vm._v("Estudie, con suma atención, el proceso de digestión de los rumiantes:")]),_c('ImagenInfografica',{staticClass:"color-primario",scopedSlots:_vm._u([{key:"imagen",fn:function(){return [_c('figure',[_c('img',{staticClass:"img-puntos-calientes-tema2",attrs:{"src":require("@/assets/curso/temas/img-puntos-calientes-tema2.png"),"alt":""}})])]},proxy:true}])},[_c('div',{staticClass:"tarjeta fondo-color-tarj-pto p-3",attrs:{"x":"10%","y":"67%","numero":"1"}},[_c('div',{staticClass:"h5 mb-2"},[_vm._v("Panza o rumen")]),_c('p',[_vm._v("Es el más voluminoso.")]),_c('p',[_vm._v("Presenta una pared delgada.")]),_c('p',[_vm._v("Su interior está recubierto por una mucosa dura.")]),_c('p',[_vm._v("Compuesto por un saco ventral y uno dorsal.")]),_c('p',[_vm._v("Se comunica con el esófago por medio del cardias.")])]),_c('div',{staticClass:"tarjeta fondo-color-tarj-pto p-3",attrs:{"x":"77%","y":"25%","numero":"2"}},[_c('div',{staticClass:"h5 mb-2"},[_vm._v("La redecilla o retículo ")]),_c('p',[_vm._v("Su forma interna presenta diseño de alveolos.")]),_c('p',[_vm._v("Posee dos orificios: uno que comunica con el rumen y otro que comunica con el librillo; y una gotera esofágica.")]),_c('p',[_vm._v("Los alimentos se mueven libremente entre el rumen y la redecilla.")])]),_c('div',{staticClass:"tarjeta fondo-color-tarj-pto p-3",attrs:{"x":"65%","y":"10%","numero":"3"}},[_c('div',{staticClass:"h5 mb-2"},[_vm._v("Librillo u omaso")]),_c('p',[_vm._v("Su forma da la impresión de hojas de un libro.")]),_c('p',[_vm._v("Es el encargado de empujar el alimento desde la redecilla hasta el abomaso.")])]),_c('div',{staticClass:"tarjeta fondo-color-tarj-pto p-3",attrs:{"x":"41%","y":"68%","numero":"4"}},[_c('div',{staticClass:"h5 mb-2"},[_vm._v("Cuajar o abomaso")]),_c('p',[_vm._v("Llamado estómago verdadero, su forma es como la de una pera.")]),_c('p',[_vm._v("Posee glándulas secretoras de jugo gástrico.")])]),_c('div',{staticClass:"tarjeta fondo-color-tarj-pto p-3",attrs:{"x":"28%","y":"92%","numero":"5"}},[_c('div',{staticClass:"h5 mb-2"},[_vm._v("Degradar alimentos")]),_c('p',[_vm._v("Acción de almacenar, fermentar, absorber las sustancias nutritivas de la degradación microbiana de los alimentos y realizar el proceso de la biosíntesis.")])]),_c('div',{staticClass:"tarjeta fondo-color-tarj-pto p-3",attrs:{"x":"44%","y":"92%","numero":"6"}},[_c('div',{staticClass:"h5 mb-2"},[_vm._v("Cuerpos sólidos")]),_c('p',[_vm._v("Lugar donde caen los cuerpos extraños de los alimentos sólidos.")])]),_c('div',{staticClass:"tarjeta fondo-color-tarj-pto p-3",attrs:{"x":"61%","y":"92%","numero":"7"}},[_c('div',{staticClass:"h5 mb-2"},[_vm._v("Triturar y secar")]),_c('p',[_vm._v("Su función es triturar y secar los alimentos, y comprimirlos entre sus láminas. Facilita el proceso de la rumia.")])]),_c('div',{staticClass:"tarjeta fondo-color-tarj-pto p-3",attrs:{"x":"77%","y":"92%","numero":"8"}},[_c('div',{staticClass:"h5 mb-2"},[_vm._v("Metabolismo")]),_c('p',[_vm._v("Parte importante para el proceso de metabolismo, lugar de secreción de ácidos y enzimas digestivas.")])])]),_c('p',{staticClass:"texto-final-punto-caliente pt-5"},[_vm._v("Por lo general, los herbívoros incluyen mucho material vegetal dentro de sus dietas. La celulo