imentos por cierto tiempo para ser ablandados y terminar de humedecerlos.")]),_c('p',{attrs:{"numero":"4","titulo":"Proventrículo"}},[_vm._v("Es un compartimiento pequeño donde se producen y se segregan los jugos gástricos.")]),_c('p',{attrs:{"numero":"5","titulo":"Molleja"}},[_vm._v("Bolsa más amplia con pared muscular que contiene arena y piedras para triturar los alimentos, cumple una función parecida a la de los dientes de las demás especies vertebradas.")]),_c('p',{attrs:{"numero":"6","titulo":"Intestino delgado"}},[_vm._v("Lugar donde está contenida la molleja, aquí desembocan los conductos del páncreas y del hígado que llevan los jugos encargados de facilitar la absorción de los nutrientes.")]),_c('p',{attrs:{"numero":"7","titulo":"Ciegos"}},[_vm._v("Son dos tubos que se encuentran en el final del intestino delgado y se conectan en un mismo punto.")]),_c('p',{attrs:{"numero":"8","titulo":"Intestino grueso"}},[_vm._v("Es la parte final del aparato digestivo que termina en la abertura común a las vías digestivas, genitales y urinarias, denominada cloaca.")])])],1)]),_c('br'),_c('br'),_c('br'),_c('p',[_vm._v("La forma como las aves procesan, a través de sus sistemas digestivos; conózcala y compréndala, analizando la siguiente imagen:")]),_vm._m(9),_c('img',{staticClass:"img-a img-t",attrs:{"data-aos":"fade-left","src":require("@/assets/curso/temas/img-sist-digestivo-aves-tema2.png"),"alt":"alt"}}),_c('p',{staticClass:"pt-5"},[_vm._v("Habiendo realizado un reconocimiento de los sistemas digestivos animales y sus generalidades, es clave comprender que la nutrición es el proceso mediante el cual se obtienen los productos de los alimentos para, posteriormente, destinarlos hacia los procesos vitales donde sean requeridos. Por consiguiente, este proceso se convierte en el eslabón más importante de la producción animal.")])],1)],1)}
